.aPropos{padding:1rem 0;background-image:linear-gradient(to bottom, #FD6C9E 0%, #FD6C9E 33%, rgb(255, 255, 255) 100%);display:flex;align-items:center}.container{max-width:1200px;margin:0 auto;display:flex;align-items:center;padding:0 2rem}.photoContainer{flex:1;padding-right:2rem}.photo{width:100%;max-width:400px;border-radius:10%;box-shadow:0 10px 20px rgba(0,0,0,.1)}.textContainer{flex:2}.textContainer h1{font-size:2.5rem;color:#fff;text-shadow:-1px -1px 0 #000,1px -1px 0 #000,-1px 1px 0 #000,1px 1px 0 #000}.textContainer h1 span{font-size:1.5rem;font-style:italic;color:inherit;text-shadow:none;display:inline-block;transform:rotate(-3deg)}.textContainer p{font-size:1.1rem;line-height:1.6;color:#555;margin-bottom:1rem}.textContainer ul{list-style-type:none;padding-left:0}.textContainer ul li{font-size:1.1rem;color:#555;margin-bottom:.5rem}.textContainer ul li:before{content:"•";color:#ff6f61;font-weight:bold;display:inline-block;width:1em;margin-left:-1em}@media(max-width: 768px){.container{flex-direction:column;text-align:center}.photoContainer{padding-right:0;margin-bottom:.5rem}.photo{max-width:300px}.textContainer ul{text-align:left}}